'use client';

import { useAppSelector } from '@/app/configs/redux/store';
import { usePathname, useRouter } from 'next/navigation';
import { ReactNode, useEffect, useState } from 'react';
import { GlobalLoading } from '../global-loading';
import { setClientGlobalStateRedux } from '@/app/helpers';

interface AuthMiddlewareProps {
  children: ReactNode;
}

export const AuthMiddleware = ({ children }: AuthMiddlewareProps) => {
  const navigate = useRouter();
  const pathname = usePathname();
  const client = useAppSelector((state) => state.client);
  const [isLoading, setIsLoading] = useState<boolean>(true);

  const handleLastAccess = async () => {
    try {
      await setClientGlobalStateRedux();
      setIsLoading(false);
    } catch {
      setIsLoading(false);
      navigate.push('/login');
    }
  };

  useEffect(() => {
    handleLastAccess();
  }, [client, navigate, pathname]);

  if (isLoading) {
    return <GlobalLoading />;
  }

  return children;
};
